#about {
  background-color: var(--color-even-section-bg);
}
.about_container {
  display: grid;
  grid-template-columns: 30% 55%;
  gap: 10%;
}
.about_me {
  width: 80%;
  aspect-ratio: 1/1;
  display: grid;
  place-items: center;
  border-radius: 100%;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
}
.about_me_image {
  width: 100%;
  border-radius: 50%;
  overflow: hidden;
  aspect-ratio: 1/1;
  transform: translate(15px) rotate(10deg);
  transition: var(--transition);
}
.about_me_image:hover {
  transform: translate(0px) rotate(0deg);
}
.about_cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.2rem;
}
.about_card {
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  background-color: var(--color-about-cards);
  transition: var(--transition);
  position: relative;
}
.about_card:hover {
  background: transparent;
  border-color: var(--color-primary-varient);
}
.about_card_icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.about_card h5 {
  font-size: 0.9rem;
}
.about_card small {
  font-size: 0.7rem;
  color: var(--color-light);
}
.about_content p {
  color: var(--color-light);
  margin: 2rem 0 2.6rem 0;
  font-weight: 400;
}
/* ============== MEDIUM DEVICES ============== */
@media screen and (max-width: 1024px) {
  .about_container {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .about_me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
}

/* ============== SMALL DEVICES ============== */
@media screen and (max-width: 600px) {
  .about_me {
    width: 60%;
    margin: 0 auto 3rem;
  }
  .about_cards {
    grid-template-columns: repeat(1, 1fr);
    gap: 1rem;
  }
  .about_content {
    text-align: center;
  }
}
