.app__work-portfolio {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  /* gap: 1.5rem */
}
.app__work-filter {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.app__work-filter-item {
  padding: 0.2rem 0.7rem;
  border-radius: 0.3rem;
  background-color: #fff;
  color: #000;
  font-weight: 500;

  cursor: pointer;
  transition: all 0.3s ease;
  margin: 0.8rem;
  margin-bottom: 1.2rem;
  margin-top: 0;
}
.app__work-filter-item:hover,
.item-active {
  background-color: var(--primary-btn-bg-color);
  color: #fff;
}
.app__work-item {
  display: flex;
  width: 270px;
  flex-direction: column;
  margin: 1rem;
  padding: 1rem;
  border-radius: 0.8rem;
  background: var(--color-projectCard-bg);
  color: var(--color-text);
  cursor: pointer;
  transition: all 0.3s ease;
  border: 1px solid var(--color-projectCard-border);
}
.app__work-item:hover {
  border-color: var(--color-projectCard-border-hover);
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
  transform: scale(1.05);
  transition: all 0.3s ease;
}
.app__work-img {
  width: 100%;
  /* height: 250px; */
}
.app__work-img > img {
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
  object-fit: cover;
}
.app__work-hover {
  display: flex;
  justify-content: space-between;
  margin-top: auto;
}
.portfolio_item_CTA {
  display: flex;
  justify-content: space-between;
  margin-top: auto;
}
.project_techstacks {
  display: flex;
  gap: 0.2rem;
  width: 100%;
  margin-top: 0.8rem;
  overflow: hidden;
}
.project_techstacks img {
  width: 30px;
}
.p-text {
  color: var(--color-light);
  font-size: 0.8rem;
  font-weight: 200;
  margin: 0 0 1rem;
}
.projectCount {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  transition: all 0.3s ease;
}
.showAll {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 1rem;
  /* text-decoration: underline; */
  margin-top: 1rem;
  margin-right: 5rem;
  cursor: pointer;
  color: rgb(8, 108, 126);
  transition: all 0.3s ease;
}
.showAll:hover {
  color: #fff;
  transition: all 0.3s ease;
}
