/* ============== MEDIUM DEVICES ============== */
@media screen and (max-width: 1024px) {
  .app__work-portfolio {
    grid-template-columns: 1fr 1fr;
    /* gap: 1.2rem; */
  }
}

/* ============== SMALL DEVICES ============== */
@media screen and (max-width: 600px) {
  .app__work-portfolio {
    grid-template-columns: 1fr;
    /* gap: 1rem; */
  }
}
