.light-theme{
  --text-color:#2a2b37;
  --text-grey:#808080;
  --color-bg:#f5f5f5;
  --navbar-bg:#f5f5f5;
  --nav-Text-color:#2a2b37;
}
.dark-theme{
  --text-color:#eeeeee;
  --text-grey:#f5f5f5;
  --color-bg:#0e1929;
  --navbar-bg:#f5f5f5;
  --nav-Text-color:#0e1929;
}

.light-theme {
  --primary-btn-color: #fff;
  --primary-btn-bg-color: #1081e9;
  --color-nav-bg: rgba(179, 179, 179, 0.25);
  --color-nav-border: rgba(177, 177, 177, 0.397);
  --color-nav-btn-hover: #2a2b37;
  --color-bg: #edf2f8;
  --color-even-section-bg: #dfe5ecb9;
  --color-bg-varient: #d8d8d8;
  --color-about-cards: #fff;
  --color-about-text: transparent;
  --color-projectcard-bg: #87b0ec;
  --color-primary: #1081e9;
  --color-primary-varient: rgb(8, 108, 126);
  --color-text: #2a2b37;
  --color-white: #fff;
  --color-light: #222222e1;
  --color-projectCard-bg: #fff;
  --color-projectCard-border-hover: transparent;
  --color-projectCard-border: transparent;
  --color-projectCard-showall-button: rgb(7, 89, 104);
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
  --transition: all 400ms ease;
  --border-size: 2px;
  --circle-color: rgba(5, 5, 5, 0.171);
}
.dark-theme {
  --primary-btn-color: #fff;
  /* --primary-btn-bg-color:rgb(6, 157, 184); */
  --primary-btn-bg-color: #1081e9;
  --color-nav-bg: rgba(110, 110, 110, 0.25);
  --color-nav-border: rgba(228, 228, 228, 0.219);
  --color-nav-btn-hover: #fff;
  --color-bg: #071833;
  --color-even-section-bg: #0b1d3b;
  --color-bg-varient: #142641;
  --color-about-cards: rgba(20, 38, 65, 0.36);
  --color-about-text: rgba(20, 38, 65, 0.36);
  --color-primary: rgb(6, 157, 184);
  --color-primary-varient: rgb(8, 108, 126);
  --color-text: #eeeeee;
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);
  --color-projectCard-bg: transparent;
  --color-projectCard-border: #0f2850;
  --color-projectCard-border-hover: rgb(8, 108, 126);
  --color-projectCard-showall-button: #fff;
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
  --transition: all 400ms ease;
  --border-size: 2px;
  --circle-color: rgba(255, 255, 255, 0.075);
}
body {
  background-color: var(--color-bg);
  color: var(--color-text);
  line-height: 1.7;
}

/* ============== GENERAL STYLES ============== */
.cursor {
  border: 1px solid var(--color-text);
  height: 32px;
  width: 32px;
  border-radius: 50%;
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 10;
}
.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}
h1 {
  font-size: 3rem;
  font-weight: 600;
}

section {
  padding-top: 5rem;
  padding-bottom: 2rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
}
a {
  color: var(--color-primary);
  transition: var(--transition);
}
a:hover {
  color: var(--color-nav-btn-hover);
}
.btn {
  width: max-content;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  color: var(--color-primary);
  padding: 0.4rem 1rem;
  border-radius: 0.4rem;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
  cursor: pointer;
}
.btn:hover {
  background-color: var(--color-text);
  color: var(--color-bg);
  border-color: transparent;
}
.btn-primary {
  width: max-content;
  display: inline-block;
  padding: 0.4rem 1rem;
  border-radius: 0.4rem;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
  background-color: var(--primary-btn-bg-color);
  color: var(--primary-btn-color);
}
.btn-primary:hover {
  background-color: var(--color-text);
  color: var(--color-bg);
  border-color: transparent;
}
img {
  display: block;
  width: 100%;
  object-fit: cover;
}
.type {
  font-size: 0.8rem;
  font-weight: 500;
  color: var(--color-light);
}
.backPattern {
  opacity: 0.5;
  width: 10%;
  position: absolute;
  z-index: -1;
  left: 90%;
  top: 20%;
  transform: translateX(-100%);
}

/* ============== MEDIUM DEVICES ============== */
@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }
  section {
    margin-top: 0rem;
  }
}

/* ============== SMALL DEVICES ============== */
@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }
  section > h2 {
    margin-bottom: 2rem;
  }
}
