#contact {
  background: var(--color-even-section-bg);
}

.contact_container {
  display: grid;
  grid-template-columns: 1fr;
  padding: 0px 5%;
  padding-bottom: 3rem;
  position: relative;
  z-index: 1;
}
.contact-firstcontent {
  width: 100%;
  display: flex;
  gap: 5%;
  justify-content: center;
}
.contact_options {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.contact_option {
  background-color: var(--color-about-cards);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  transition: var(--transition);
  backdrop-filter: blur(3px) saturate(113%);
  -webkit-backdrop-filter: blur(9px) saturate(113%);
  border: 1px solid rgba(255, 255, 255, 0.125);
}
.contact_option:hover {
  background: transparent;
  border-color: var(--color-primary-varient);
  transform: scale(1.05);
}
.contact_option a :hover {
  border-color: var(--color-primary-varient);
  background: transparent;
  transform: scale(1.05);
}
.contact_option-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}
.contact_option a {
  display: block;
  font-size: 0.8rem;
  margin-top: 0.7rem;
}

