/* ============== FORM ============== */
form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  width: 50%;
}

input {
  height: 20%;
}
textarea {
  height: 100%;
}

input,
textarea {
  width: 100%;
  padding: 1.2rem;
  border-radius: 0.5rem;
  border: 2px solid var(--color-primary-varient);
  resize: none;
  color: var(--color-text);
  backdrop-filter: blur(3px) saturate(113%);
  -webkit-backdrop-filter: blur(9px) saturate(113%);
  background-color: var(--color-about-cards);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.125);
}
input::placeholder,
textarea::placeholder {
  color: var(--color-light);
  font-size: 0.9rem;
}

/* ============== MEDIUM DEVICES ============== */
@media screen and (max-width: 1024px) {
  /* .contact-firstcontent{
      flex-direction: column;
      gap: 2rem;
    } */
  .contact_container {
    display: flex;
    flex-direction: column-reverse;
  }
}

/* ============== SMALL DEVICES ============== */
@media screen and (max-width: 600px) {
  .contact-firstcontent {
    flex-direction: column;
    gap: 2rem;
  }
  .contact_container {
    display: flex;
    flex-direction: column-reverse;
  }
  .contact_options {
    gap: 0.8rem;
  }

  form {
    width: 100%;
  }
}
