header {
  height: 400px;
  padding-top: 8rem;
  overflow: hidden;
}
.header_container {
  text-align: center;
  /* height: 100%; */
  position: relative;
}
.header_container > h5 {
  font-size: 1.2rem;
}
.header_container > h1 {
  font-size: 4rem;
}

/* ===================== CTA ===================== */

.cta {
  margin-top: 1.1rem;
  display: flex;
  gap: 1rem;
  justify-content: center;
}

/* ===================== Header Socials ===================== */

.header_social{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  position: absolute;
  left: 0;
  top: 3rem;
}
.bottom_header_social {
  display: none;
  align-items: center;
  gap: 2rem;
  margin-top: 1rem;
  justify-content: center;
}
.header_social a:hover,
.bottom_header_social a:hover {
  transform: scale(1.8);
}
.circle-Head {
  margin-top: 1.1rem;
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}
.ping1 {
  position: absolute;
  top: 0;
  border-radius: 50%;
  border: 1px solid var(--circle-color);
  height: 120px;
  width: 120px;
  z-index: -1;
  animation: ping1 2.5s ease-out infinite;
}
@keyframes ping1 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(3);
    opacity: 0;
  }
}
