  content: '';
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
} */

/* ===================== Image ===================== */

.me {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 3rem;
  border-radius: 12rem;
  overflow: hidden;
  opacity: 0.8;
}

/* ===================== scroll down ===================== */

.scroll_down{
  position: absolute;
  right: -2.3rem;
  top: 5rem;
  transform: rotate(90deg);
  font-weight: 200;
  font-size: 0.9rem;
}

/* ============== MEDIUM DEVICES ============== */
@media screen and (max-width: 1024px) {
  header {
    height: 360px;
  }
  .header_social {
    display: flex;
    position: absolute;
    top: 1.2rem;
    left: 0.8rem;
  }
  .scroll_down {
    display: none;
  }
  .header_container > h1 {
    font-size: 3rem;
  }

  .bottom_header_social {
    display: none;
  }
}

/* ============== SMALL DEVICES ============== */
@media screen and (max-width: 600px) {
  .header_social, .scroll_down {
    display: none;
  }
  .scroll_down {
    display: none;
  }
  .header_container > h1 {
    font-size: 3rem;
  }
  header {
    height: 480px;
  }
  .bottom_header_social {
    display: flex;
  }
}
